/* Poppins */
@font-face {
  font-family: "Poppins";
  src: local("Poppins Light"), local("Poppins-Light"), url("../fonts/Poppins-Light.woff2") format("woff2"), url("../fonts/Poppins-Light.woff") format("woff"), url("../fonts/Poppins-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins Regular"), local("Poppins-Regular"), url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff"), url("../fonts/Poppins-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins Medium"), local("Poppins-Medium"), url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff"), url("../fonts/Poppins-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins SemiBold"), local("Poppins-SemiBold"), url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff"), url("../fonts/Poppins-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins ExtraBold"), local("Poppins-ExtraBold"), url("../fonts/Poppins-ExtraBold.woff2") format("woff2"), url("../fonts/Poppins-ExtraBold.woff") format("woff"), url("../fonts/Poppins-ExtraBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins Bold"), local("Poppins-Bold"), url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff"), url("../fonts/Poppins-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
/* FuturaBookC */
@font-face {
  font-family: "FuturaBookC";
  src: local("FuturaBookC"), url("../fonts/FuturaBookC.woff2") format("woff2"), url("../fonts/FuturaBookC.woff") format("woff"), url("../fonts/FuturaBookC.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/* FuturaFuturisC */
@font-face {
  font-family: "FuturaFuturisC";
  src: local("FuturaFuturisC"), url("../fonts/FuturaFuturisC.woff2") format("woff2"), url("../fonts/FuturaFuturisC.woff") format("woff"), url("../fonts/FuturaFuturisC.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "FuturaFuturisC";
  src: local("FuturaFuturisC Bold"), local("FuturaFuturisC-Bold"), url("../fonts/FuturaFuturisC-Bold.woff2") format("woff2"), url("../fonts/FuturaFuturisC-Bold.woff") format("woff"), url("../fonts/FuturaFuturisC-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
/* FuturaDemiC */
@font-face {
  font-family: "FuturaDemiC";
  src: local("FuturaDemiC"), url("../fonts/FuturaDemiC.woff2") format("woff2"), url("../fonts/FuturaDemiC.woff") format("woff"), url("../fonts/FuturaDemiC.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/* FuturaLightC */
@font-face {
  font-family: "FuturaLightC";
  src: local("FuturaLightC"), url("../fonts/FuturaLightC.woff2") format("woff2"), url("../fonts/FuturaLightC.woff") format("woff"), url("../fonts/FuturaLightC.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
/* Proxima Nova */
@font-face {
  font-family: "Proxima Nova";
  src: local("Proxima Nova Regular"), local("ProximaNova-Regular"), url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("../fonts/ProximaNova-Regular.woff") format("woff"), url("../fonts/ProximaNova-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  font-size: 2rem;
  min-width: 320px;
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 400;
  background-color: #f2f2f4;
  color: #000000;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  width: 128.1rem;
}

.logo {
  display: flex;
  align-items: center;
  transition: all 0.2s linear;
}
.logo:focus {
  outline: 3px solid #fff;
}
.logo:hover {
  opacity: 0.6;
}
.logo:active {
  opacity: 0.3;
}

.form {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 1rem;
}
.form__wrapper {
  display: flex;
  flex-direction: column;
  padding: 4rem;
}
.form__title {
  margin: 0;
  padding: 5rem 0rem 0rem 6rem;
  width: 38rem;
  height: 11rem;
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 700;
  font-size: 2.3rem;
  text-align: center;
  color: #fff;
  background: linear-gradient(180deg, rgb(106, 50, 180) 0%, rgb(85, 38, 147) 100%);
  border-radius: 1rem 1rem 0 0;
}
.form__label {
  display: block;
  margin-left: 2rem;
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.3rem;
  color: #626366;
  cursor: pointer;
}
.form__label:not(:first-of-type) {
  margin-top: 1.5rem;
}
.form__input {
  padding: 2rem;
  margin-top: 0.8rem;
  width: 100%;
  height: 5.6rem;
  border-radius: 1rem;
  border: none;
  box-shadow: inset 1px 1px 3px 0 rgba(0, 0, 0, 0.1);
  background: #f5f3ff;
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.6rem;
  color: #000;
}
.form__input::placeholder {
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.6rem;
  color: #bcbfc4;
}
.form__button {
  margin-top: 3.4rem;
}
.form__copyright {
  position: relative;
  display: inline-block;
  margin-top: 1.7rem;
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  color: #c0c0c0;
  align-self: center;
}
.form__copyright::after {
  content: "";
  position: absolute;
  top: 50%;
  left: -2rem;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background: url(../img/form/lock.svg) center no-repeat;
}

.title {
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 700;
  font-size: 4.5rem;
  margin: 0;
  text-align: center;
  line-height: 4.5rem;
}
.title--black {
  color: #111;
}
.title--white {
  color: #fff;
}

.button {
  display: block;
  padding-left: 5rem;
  width: 100%;
  height: 5.6rem;
  border: none;
  border-radius: 1rem;
  background-color: #ffa500;
  cursor: pointer;
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 400;
  font-size: 1.8rem;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  transition: all 0.2s linear;
}
.button:focus {
  outline: 3px solid #fff;
}
.button:hover {
  opacity: 0.6;
}
.button:active {
  opacity: 0.3;
}

.header {
  background: #f2f2f4 url(../img/header/bg.png) 100%/cover no-repeat;
  min-height: 88rem;
}
.header__wrapper {
  padding-top: 5.5rem;
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header picture,
.header picture img {
  width: 155px;
  height: 28px;
}
.header__list {
  margin: 0;
  padding: 0;
  display: flex;
  column-gap: 1rem;
  list-style: none;
}
.header__item-link {
  display: inline-block;
  padding-left: 1rem;
  padding-right: 1rem;
  font-family: "FuturaLightC", sans-serif;
  font-weight: 200;
  line-height: 2.2rem;
  text-decoration: none;
  color: #fff;
  transition: all 0.2s linear;
}
.header__item-link:focus {
  outline: 3px solid #fff;
}
.header__item-link:hover {
  opacity: 0.6;
}
.header__item-link:active {
  opacity: 0.3;
}
.header__phone-link {
  position: relative;
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.2rem;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  transition: all 0.2s linear;
}
.header__phone-link:focus {
  outline: 3px solid #fff;
}
.header__phone-link:hover {
  opacity: 0.6;
}
.header__phone-link:active {
  opacity: 0.3;
}
.header__phone-link::after {
  content: "";
  position: absolute;
  top: 50%;
  left: -4rem;
  transform: translateY(-50%);
  width: 33px;
  height: 33px;
  background: url("../img/icon/phone-icon.svg") center no-repeat;
}
.header__promo {
  margin-top: 11rem;
  display: grid;
  grid-template-columns: 1fr 38rem;
  grid-template-rows: repeat(3, auto);
  grid-template-areas: "title form" "description form" "advantages form";
  column-gap: 5rem;
}
.header__title {
  grid-area: title;
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 700;
  font-size: 6.9rem;
  line-height: 6.9rem;
  text-transform: uppercase;
  color: #fff;
}
.header__title--action {
  font-size: 4.2rem;
  letter-spacing: 0.44rem;
  color: #969af7;
}
.header__title--size {
  font-size: 6.1rem;
  line-height: 7.5rem;
}
.header__description {
  grid-area: description;
  margin-top: 0.4rem;
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2.3rem;
  line-height: 3rem;
  color: #969af7;
}
.header__list-advantages {
  display: block;
  grid-area: advantages;
  padding: 2rem 2rem 2rem 5.8rem;
  margin: 0;
  list-style: none;
  background: linear-gradient(90deg, rgba(150, 154, 247, 0.7) 0%, rgba(150, 154, 247, 0) 100%);
  border-radius: 1rem;
}
.header__item-advantages {
  position: relative;
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2.2rem;
  line-height: 2.9rem;
  color: #fff;
}
.header__item-advantages::after {
  content: "";
  position: absolute;
  width: 14px;
  height: 11px;
  top: 50%;
  left: -2.5rem;
  transform: translateY(-50%);
  background: url(../img/header/check.svg) center no-repeat;
}
.header__item-advantages:not(:last-of-type) {
  margin-bottom: 0.4rem;
}
.header__form {
  grid-area: form;
}

.why {
  padding-top: 8.5rem;
  padding-bottom: 13rem;
}
.why__title {
  text-align: center;
}
.why__description {
  padding-left: 11.5rem;
  padding-right: 11.5rem;
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2.2rem;
  line-height: 109%;
  text-align: center;
  color: #030303;
}
.why__list {
  padding: 0 4.3rem;
  margin: 0;
  margin-top: 4.2rem;
  display: flex;
  flex-wrap: wrap;
  gap: 4.3rem;
  list-style: none;
}
.why__item {
  padding: 4rem 2.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 36.9rem;
  background: #fff url(../img/why/bg-card.png) 100% 100% no-repeat;
  border-radius: 1rem;
}
.why__item-title {
  margin-top: 4rem;
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  color: #2a336c;
}
.why__item-descr {
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  line-height: 110%;
  text-align: center;
  color: #555;
}
.why__img {
  stroke: #fff;
}

.manufacturers {
  background: url(../img/manufacturers/bg.png) 100%/cover no-repeat;
  min-height: 65rem;
}
.manufacturers__wrapper {
  padding-top: 15rem;
}
.manufacturers__slider {
  position: relative;
  margin-top: 5.7rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.manufacturers__slider-list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
  column-gap: 1.8rem;
}
.manufacturers__slider-item,
.manufacturers .swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24.2rem;
  height: 17.4rem;
  background-color: #fff;
  border-radius: 1rem;
}
.manufacturers__description {
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  line-height: 155%;
  color: #fff;
}
.manufacturers__button,
.manufacturers .swiper-button-next,
.manufacturers .swiper-button-prev {
  position: absolute;
  width: 4rem;
  height: 4rem;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  border: none;
  transition: all 0.2s linear;
  z-index: 10;
}
.manufacturers__button:focus,
.manufacturers .swiper-button-next:focus,
.manufacturers .swiper-button-prev:focus {
  outline: 3px solid #fff;
}
.manufacturers__button:hover,
.manufacturers .swiper-button-next:hover,
.manufacturers .swiper-button-prev:hover {
  opacity: 0.6;
}
.manufacturers__button:active,
.manufacturers .swiper-button-next:active,
.manufacturers .swiper-button-prev:active {
  opacity: 0.3;
}
.manufacturers__button:after,
.manufacturers .swiper-button-next:after,
.manufacturers .swiper-button-prev:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  width: 6.5px;
  height: 13px;
  background: url(../img/manufacturers/arrow.svg) center no-repeat;
}
.manufacturers__button-wrapper {
  position: relative;
  top: 14rem;
  overflow: visible;
}

#manufacturers__button--prev,
#manufacturers__button--next {
  width: 4rem;
  height: 4rem;
  z-index: 10;
}

#manufacturers__button--prev {
  left: -7rem;
}

#manufacturers__button--next {
  right: -7rem;
  transform: rotate(180deg);
}

.scheme {
  background: url(../img/scheme/bg.png) 100%/cover no-repeat;
}
.scheme__wrapper {
  padding-top: 13rem;
  padding-bottom: 13rem;
}
.scheme__cards {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style: none;
  counter-reset: number;
}
.scheme__card {
  position: relative;
  padding: 4.8rem 3rem 3rem 10rem;
  background: linear-gradient(45deg, #041080 9%, #0926c0 100%);
  border-radius: 3rem;
  width: 41rem;
  min-height: max-content;
}
.scheme__card:nth-child(4) {
  order: 6;
}
.scheme__card:nth-child(5) {
  order: 5;
}
.scheme__card:nth-child(6) {
  order: 4;
}
.scheme__card::after {
  position: absolute;
  top: 5.5rem;
  left: 3rem;
  counter-increment: number;
  content: "0" counter(number);
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 700;
  font-size: 3.6rem;
  text-align: center;
  color: #969af7;
}
.scheme__card-title {
  font-family: "FuturaFuturisC", sans-serif;
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 139%;
  color: #fff;
}
.scheme__card-description {
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 139%;
  color: #e3e3e3;
}
.scheme__button {
  display: block;
  margin: 0 auto;
  margin-top: 4.9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  width: 30rem;
}

.answers {
  margin-top: -10rem;
  background: url(../img/answer/bg.png) 100%/cover no-repeat;
}
.answers__wrapper {
  padding-top: 12rem;
  padding-bottom: 12rem;
}
.answers__accordion {
  margin-top: 4rem;
}
.answers__accordion-title {
  display: block;
  position: relative;
  padding: 3rem 5.3rem;
  width: 100%;
  height: 8.7rem;
  box-shadow: 0 0 50px -10px rgba(22, 126, 194, 0.2);
  background: linear-gradient(180deg, #ffffff 0, #bdc0ff 100%);
  border: none;
  border-radius: 1rem;
  font-family: "FuturaDemiC", sans-serif;
  font-weight: 600;
  font-size: 2.4rem;
  line-height: 117%;
  color: #333;
  text-align: left;
  transition: all 0.2s linear;
}
.answers__accordion-title:not(:first-child) {
  margin-top: 1rem;
}
.answers__accordion-title:before {
  position: absolute;
  content: "";
  top: 50%;
  right: 3rem;
  transform: translateY(-50%);
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #757aef;
  transition: all 0.2s linear;
}
.answers__accordion-title:after {
  position: absolute;
  content: "";
  top: 41%;
  right: 4.3rem;
  transform: translateY(-50%);
  width: 1.4rem;
  height: 1.4rem;
  background: url(../img/manufacturers/arrow.svg) center no-repeat;
  transform: rotate(-90deg);
  transition: all 0.2s linear;
}
.answers__accordion-title--active {
  color: #fff;
  background: linear-gradient(180deg, #969af7 0, #686eec 100%);
}
.answers__accordion-description {
  margin-top: -0.7rem;
  padding: 5.2rem;
  background-color: #fff;
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  line-height: 135%;
  color: #333;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  display: none;
  transition: all 0.2s linear;
}

.answers__accordion-title--active.answers__accordion-title::before {
  background-color: #fff;
}

.answers__accordion-title--active.answers__accordion-title::after {
  background-color: #fff;
  box-shadow: 0 4px 30px 0 rgba(166, 190, 226, 0.5);
  background: none;
  background: url(../img/answer/arrow-blue.svg) no-repeat center;
  transform: rotate(0);
}

.footer {
  background: #333333;
}
.footer__wrapper {
  padding-top: 7.2rem;
  padding-bottom: 7.2rem;
}
.footer__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__list {
  display: flex;
  list-style: none;
  column-gap: 3.6rem;
}
.footer__item-link {
  font-family: "FuturaBookC", sans-serif;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.2rem;
  color: #fff;
  text-decoration: none;
  transition: all 0.2s linear;
}
.footer__item-link:focus {
  outline: 3px solid #fff;
}
.footer__item-link:hover {
  opacity: 0.6;
}
.footer__item-link:active {
  opacity: 0.3;
}
.footer__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 26.1rem;
  background-color: #969af7;
  text-decoration: none;
}